body, html{
    height: 100%;
    width: 100vw;
    #root{
        height: 100%;
    }
    font-size: 13px;
}

h1{
    font-size: 1.5rem;
}

.nav{
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    z-index: 2;
    .logo{
        a{
            text-decoration: none;
        }
    }
    .container{
        position: relative;
        background-color: #ffffff00;
        padding: 0px 50px 0 50px;
        display: flex;
        justify-content: space-between;

        ul {
            margin: 20px 0 0 0;
            padding: 0;
            list-style: none;
            display: flex;
            justify-content: space-between;
            width: 320px;
        }
        
        li { 
            display: inline-block; 
        }
        
        a {
            padding: 7px 15px;
            display: block;
            text-decoration: none;
            border-radius: 20px;
            color: #000;
            text-shadow: 4em;
            font-weight: 400;
        }

        .active{
            background-color: grey;
        }
    }

    
}

.legendContent{
    width: 100%;
    position: relative;
    letter-spacing: 0.012em;
    .buttonWrap{
        margin: 80px 0 0 50px;
        position: relative;
        display: flex;
        .legendButtons{
            a{
                text-decoration: none;
            }
            .legendButton{
                border-radius: 15px;
                font-size: 16px;
                letter-spacing: 1px;
                padding: 5px 15px;
                margin: 5px;
                box-shadow: 2px 5px 20px 7px rgba(0,0,0,0.12), -1px -2px 10px 2px rgba(0,0,0,0.11);
                cursor: pointer;
                text-align: center;
                &:active{
                    box-shadow: none;
                }
            }
        }
    }
    

    .contactBtn{
        background: rgb(65, 182, 65);
        color: #fff;
    }

    .projectBtn{
        background: rgb(148, 147, 147);
        color: #fff;
    }

   

    .legendCards{
        position: relative;
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: space-around;
        margin: 50px 0 50px 0;
        .card{
            margin-top: 20px;
            padding: 10px;
            position: relative;
            width: 500px;
            background: #fff;
            border-radius: 3px;
            box-shadow: 2px 5px 20px 7px rgba(0,0,0,0.25), -1px -2px 10px 2px rgba(0,0,0,0.22);
            transition: 0.4s;
            .card-header{
                text-align: center;
            }

            .card-body{
                height: 200px;
                position: relative;
                .card-content{
                    height: 100%;
                    width: 100%;
                }
            }

            .card-footer{
                text-align: center;
                padding: 0px 0;
                a{
                    text-decoration: none;
                    color: purple;
                }
            }
        }
    }
}

.homeContainer{
    position: relative;
    //height: 100%;
    display: flex;
    .home{
        display: flex;
        position: relative;
        height: 100%;
        z-index: 0;
        width: 100%;
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;
        background-image: url("./assets/1.svg");
        .homeCover{
            position: absolute;
            height: 100%;
            display: flex;
            flex: 1 0 auto;
            background-color: rgb(240, 239, 239);
            width: 100%;
            top: 0;
            opacity: .7;
            z-index: 0;
        }
    }
}


.page{
    position: relative;
    padding: 30px 70px;
    text-align: center;
    background: #fff;
    border-radius: 5px 5px 0 0;
  
    .pageTitle{
        display: flex;
        justify-content: center;
        margin-bottom: 50px;
    }

    h1{
        font-size: 1.5rem;
        padding: 5px 10px;
    }
}

.projects{
    background: #fff;
    height: 100%;
    border-radius: 5px 5px 0 0;
    padding: 10px 20px;
    box-shadow: 0px 0px 20px 7px rgba(0,0,0,0.12), 0px 0px 0px 0px rgba(0,0,0,0.11);

    h1{
        margin-left: 5px; 
    }
    /* Style the tab */
    .tab {
        overflow: hidden;
        }
        
        /* Style the buttons inside the tab */
        .tab button {
            background-color: inherit;
            float: left;
            border: none;
            outline: none;
            cursor: pointer;
            padding: 8px 10px;
            transition: 0.3s;
            font-size: 14px;
            border-radius: 5px;
            margin: 5px;
        }
        
        /* Change background color of buttons on hover */
        .tab button:hover {
        background-color: #ddd;
        }
        
        /* Create an active/current tablink class */
        .tab button.active {
        background-color: #ccc;
        }
        
        /* Style the tab content */
        .tabcontent {
        display: none;
        padding: 20px 5px;
        border-top: none;
        .tab-card{
            width: 200px;
            height: 200px;
            box-shadow: 1px 2px 10px 3px rgba(0,0,0,0.25), -1px -2px 10px 3px rgba(0,0,0,0.22);
        }
    }
}

//Menu Icon styles
.menuIcon{
    margin:20px 0px 0 0;
    cursor: pointer;
}

.bar1, .bar2, .bar3 {
    width: 30px;
    height: 3px;
    background-color: #333;
    margin: 6px 0;
    transition: 0.4s;
}

.change .bar1 {
    -webkit-transform: rotate(-45deg) translate(-4px, 2px);
    transform: rotate(-45deg) translate(-5px, 2px);
}

.change .bar2 {opacity: 0;}

.change .bar3 {
    -webkit-transform: rotate(45deg) translate(-9px, -8px);
    transform: rotate(45deg) translate(-9px, -8px);
}

.slideMenu{
    position: absolute;
    top: 0;
    width: 100%;
    background: #fff;
    text-align: center;
    transition: .1s;
    ul{
        position: relative;
        height: 100%;
        list-style: none;
        text-decoration: none;
        padding: 50px 0 5px 0;
        cursor: pointer;
        li{
            position: relative;
            &:hover{
                border-bottom: #333 solid 1px;
            }

            &:active{
                background: rgb(236, 235, 235);
            }

            a{
                text-decoration: none;
                color: #000;
            }

            div{
                height: 50px;
                line-height: 50px;
                width: 100%;
            }
        }
    }
}

.footer{
    padding: 30px 70px;
    background: #000;
    color: #ddd;
    position: relative;
    i{
        font-size: 25px;
        padding: 7px 0;
    }

    .fa-envelope{
        font-size: 16px;
    }
    a{
        text-decoration: none;
        color: #ddd;
    }

    .copyright{
        width: 100%;
        text-align: center;
        margin-top: 30px;
    }
    .useful-links{
        position: absolute;
        right: 80px;
        top: 30px;
    }
    h2{
        font-size: 16px;
        margin-bottom: 2px; 
    }
}

@media only screen and (min-width: 1440px){
    .legendContent{
        .legendCards{
            .card{
                width: 500px;
            }
        }
    }

    .homeContainer{
        height: 100%;
    }
}

@media only screen and (max-width: 1440px){
    .legendContent{
        .legendCards{
            .card{
                width: 350px;
            }
            margin-left: 50px;
            margin-right: 50px;
        }
    }

    .homeContainer{
        height: 100%;
    }
}

@media only screen and (max-width: 1024px){
    .legendContent{
       
        .legendCards{
            
            margin-top: 50px;
            .card{
                width: 250px;
                .card-body{
                    height: 230px;
                }
            }
        }
    }

    .homeContainer{
        height: auto;
    }
}

@media only screen and (max-width: 768px){
    .legendContent{
        .buttonWrap{
            width: 100%;
            margin-top: 90px;
            margin-left: 0;
            justify-content: center;
            .legendButtons{
                width: 100%;
                display: flex;
                justify-content: center;
                .legendButton{
                    text-align: center;
                    box-shadow: none;
                    margin-top: 50px;
                } 
            }
        }
        
        .legendCards{
            margin-left: 10px;
            margin-right: 10px;
            .card{
                width: 600px;
                
            }
        }
    }

    .homeContainer{
        height: auto;
    }

    .footer{
        padding: 30px 20px 5px 20px;
        .useful-links{
            position: relative;
            top: 0;
            right: 0
        }
    }

    .menuIcon{
        margin:20px 30px 0 0;
    }
}

@media only screen and (max-width: 425px){
    .nav{
        .container{
            padding: 0 10px 0 10px;
        }
    }
    .legendContent{
        .buttonWrap{
            width: 100%;
            margin-top: 90px;
            margin-left: 0;
            justify-content: center;
            .legendButtons{
                .legendButton{
                    text-align: center;
                    box-shadow: none;
                    margin-top: 15px;
                } 
            }
        }
    }

    .menuIcon{
        margin:20px 0px 0 0;
    }

    
}
